<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar
          dark
          color="primary"
        >
          <v-btn
            icon
            dark
            @click="cerrarModalActual"
          >
            <v-icon>fas fa-times</v-icon>
          </v-btn>
          <v-toolbar-title>Editar Folio: <v-chip  dark>{{ datosBasicos.folio }}</v-chip></v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
              dark
              text
              @click="dialog = false"
            >
              guardar
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-container class="leblanc mb-6">
          <v-row>
            <v-col cols="12" md="8" class="text-left mt-10">
                <label>Nombre completo</label>
                <v-text-field
                  v-model="nombre_presidente"
                  :rules="campo_requeridoRules"
                  label=""
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4" class="text-left mt-10">
                <label>Corriente poltica</label>
                <v-select
                  :items="grupos_politicos"
                  v-model="corriente_politica_presidente"
                  label=""
                  outlined
                  placeholder="Selecciona grupo poltico"
                  item-text="label"
                  item-value="value"
                  :loading="loading"
                  attach
                  :rules="selectRules"
                ></v-select>
              </v-col>
              <!-- Segunda fila -->
              <v-col cols="12" md="4" class="text-left">
                <label>Semestre actual</label>
                <v-text-field
                  v-model="semestre_presidente"
                  :rules="campo_requeridoRules"
                  label=""
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4" class="text-left">
                <label>Sexo</label>
                <v-select
                  v-model="sexo_presidente"
                  :items="sexos"
                  label=""
                  outlined
                  placeholder="Selecciona sexo"
                  :loading="loading"
                  attach
                >
                </v-select>
              </v-col>
              <v-col cols="12" md="4" class="text-left">
                <label>Gnero</label>
                <v-text-field
                  v-model="genero_presidente"
                  :rules="campo_requeridoRules"
                  label=""
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <!-- Tercera Fila -->
              <v-col cols="12" md="4" class="text-left">
                <label>CURP</label>
                <v-text-field
                  v-model="curp_presidente"
                  :rules="campo_requeridoRules"
                  label=""
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4" class="text-left">
                <label>Cdigo U de G</label>
                <v-text-field
                  v-model="codigo_presidente"
                  :rules="codigoUGRules"
                  label=""
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4" class="text-left">
                <label>Nivel</label>
                <v-select
                  v-model="nivel_presidente"
                  :items="niveles"
                  label=""
                  outlined
                  attach
                  readonly
                ></v-select>
              </v-col>
              <!-- Cuarta fila -->
              <v-col cols="12" md="4" class="text-left">
                <label>Centro</label>
                <v-select
                  v-model="centro_presidencia"
                  :items="centros"
                  label=""
                  outlined
                  placeholder="Selecciona centro"
                  item-text="label"
                  item-value="value"
                  :loading="loading"
                  @change="postObtenerDivisiones"
                  :rules="selectRules"
                ></v-select>
              </v-col>
              <v-col cols="12" md="4" class="text-left">
                <label v-if="this.nivel_presidente !== 'Preparatoria'"
                  >Divisin</label
                >
                <label v-else>Plantel</label>
                <v-select
                  v-model="division_presidencia"
                  :items="divisiones"
                  label=""
                  outlined
                  placeholder="Selecciona divisin"
                  item-text="label"
                  item-value="value"
                  :loading="loading"
                  @change="postObtenerCarreras"
                  :rules="selectRules"
                ></v-select>
              </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import axios from "axios";
import {apiURL} from '../util/api'
  export default {
    name: "FormPlanilla",
      props: {
    dialog: Boolean,
    datosBasicos: Object
    },
    data () {
      return {
        loading: false,
        grupos_politicos: [],
        sexos: [],
        generos: [],
        niveles: ["Preparatoria", "Licenciatura"],
        centros: [],
        divisiones: [],
        carreras: [],
        turnos: [],
        notifications: false,
        sound: true,
        widgets: false,
      }
    },
    methods: {
      cerrarModalActual() {
        this.$emit(newFunction(), false)

        function newFunction() {
          return 'cerrarModalFormEdit'
        }
      },
      // LLamada a API
      async getSexo() {
      this.loading = true;
      let sexo = await axios.get(
        `${apiURL}obtainsex`
      );
      this.loading = false;
      let sx = sexo.data.data;
      Object.keys(sx).forEach((element) => {
        //console.log( sx[element].sexo);
        this.sexos.push(sx[element].sexo);
      });
    },
    async getGenero() {
      this.loading = true;
      let genero = await axios.get(
        `${apiURL}obtaingender`
      );
      this.loading = false;
      let gn = genero.data.data;
      Object.keys(gn).forEach((element) => {
        //console.log( gn[element].genero);
        this.generos.push(gn[element].genero);
      });
    },

    async getTurnos() {
      this.loading = true;
      let turno = await axios.get(
        `${apiURL}obtainturns`
      );
      this.loading = false;
      let tn = turno.data.data;
      Object.keys(tn).forEach((element) => {
        //console.log( gn[element].genero);
        this.turnos.push(tn[element].turno);
      });
    },

    async postObtenerCentrosUniversitarios() {
      this.loading = true;
      let respuesta = await axios.post(
        `${apiURL}obtaincentros`,
        {
          eleccion_id: this.eleccion_id,
        }
      );
      this.loading = false;
      let lista_centros = respuesta.data.data;
      Object.keys(lista_centros).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.centros.push({
          value: lista_centros[element].centro,
          label: lista_centros[element].centro_desc,
        });
      });
      //Se comprueba si es preparatoria
      if (this.nivel_presidente === "Preparatoria") {
        this.centros = this.centros.filter(function (centro) {
          return centro.value === "SEMS";
        });
      } else {
        this.centros = this.centros.filter(function (centro) {
          return centro.value !== "SEMS";
        });
      }
    },

    async postObtenerDivisiones() {
      this.loading = true;
      this.divisiones = [];
      let respuesta = await axios.post(
        `${apiURL}obtaindivision`,
        {
          eleccion_id: this.eleccion_id,
          centro: this.centro_presidencia,
        }
      );
      this.loading = false;
      let lista_divisiones = respuesta.data.data;
      Object.keys(lista_divisiones).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.divisiones.push({
          value: lista_divisiones[element].plantel,
          label: lista_divisiones[element].plantel,
        });
      });
    },

    async getGruposPoliticos() {
      this.loading = true;
      let grupospoliticos = await axios.get(
        `${apiURL}obtainpoliticalgroups`
      );
      this.loading = false;
      let gp = grupospoliticos.data.data;
      Object.keys(gp).forEach((element) => {
        //console.log( gp[element]);
        this.grupos_politicos.push({
          value: gp[element].id,
          label: gp[element].nombre,
        });
      });
      this.grupos_politicos = this.grupos_politicos.sort(function (a, b) {
        if (a.label > b.label) {
          return 1;
        }
        if (a.label < b.label) {
          return -1;
        }
        // a must be equal to b
        return 0;
      });
    },

    async postObtenerCarreras() {
      this.loading = true;
      this.carreras = [];
      this.vicepresidencia_ejecutiva = [];
      let respuesta = await axios.post(
        `${apiURL}obtaincarrera`,
        {
          eleccion_id: this.eleccion_id,
          centro: this.centro_presidencia,
          plantel: this.division_presidencia,
        }
      );
      this.loading = false;

      let lista_carreras = respuesta.data.data;
      Object.keys(lista_carreras).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.vicepresidencia_ejecutiva.push({nombre: "",carrera: "",turno: "",semestre: "",codigo: "",sexo: "",genero: "",files_credencial: [],files_credencialB64: "",files_kardex: [],files_kardexB64: "",
        });
        this.carreras.push({
          value: lista_carreras[element].carrera,
          label: lista_carreras[element].carrera,
        });
      });
      this.carreras = this.carreras.sort(function (a, b) {
        if (a.label > b.label) {
          return 1;
        }
        if (a.label < b.label) {
          return -1;
        }
        // a must be equal to b
        return 0;
      });
    },
    },
    mounted() {
    this.getSexo();
    this.getGenero();
    this.postObtenerCentrosUniversitarios();
    this.getGruposPoliticos();
    this.getTurnos();
  },
  }
</script>
